@import "config";
@import "menu";
@import "button";

* {
  box-sizing: border-box;
}

body {
  background: $color-primary;
  color: $text-color-primary;
  height: 100%;
  margin: 0;
  font-family: "Roboto", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
}

h1,
h2,
h3 {
  margin: 0;
  font-weight: 500;

  &.big-heading {
    font-size: 6rem;
  }
  &.small-heading {
    margin-bottom: 2rem;
    padding: 0.2rem, 1rem;
  }
}

a {
  color: $text-color-primary;
  text-decoration: none;
}

header {
  position: fixed;
  z-index: 2;
  width: 100%;
}

.text-secondary {
  color: $text-color-secondary;
}

main {
  padding: 4rem;
  height: 100%;

  .icon {
    margin-top: 1rem;

    a {
      padding: 0.4rem;

      &:hover {
        color: $color-secondary;
        @include easeOut(color);
      }
    }
  }

  &#home {
    overflow: hidden;
    h1 {
      margin-top: 20vh;
    }
  }
}

.about-info {
  display: grid;
  grid-gap: 30px;
  grid-template-areas: "bioimage bio bio";
  grid-template-columns: repeat(3, 1fr);
}

.bio-image {
  grid-area: bioimage;
  border: $color-secondary 3px solid;
  width: 250px;
  height: 250px;
}

.bio {
  grid-area: bio;
  font-size: 1.5rem;
}

.projects {
  width: 100%;
  justify-content: center;
  display: flex;
  .items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 1rem;

    .item {
      margin: 3rem 1rem 0rem 1rem;
      text-align: center;
      flex: 1;
      margin-top: 3rem;
      max-width: 20rem;
      min-width: 20rem;

      .project-img-overlay {
        display: flex;

        p {
          position: absolute;
          width: 18rem;
          padding-left: 2rem;
          opacity: 0;
        }

        &:hover {
          p {
            opacity: 1;
            @include easeOut(all);
          }
          img {
            opacity: 0.4;
            border-color: $color-secondary;
            @include easeOut(all);
          }
        }
      }
    }

    img {
      width: 100%;
      border: 3px $color-contrast solid;
    }
  }
}

.contact-boxes {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 1rem;

  div {
    font-size: 2rem;
    border: 3px $color-contrast solid;
    padding: 1.5rem 2.5rem;
    margin-bottom: 3rem;
    @include easeOut(all);

    &:hover {
      padding: 0.5rem 1.5rem;
      background: $color-secondary;
      color: $color-contrast;
      span {
        color: $color-contrast;
      }
    }
  }
}

@import "mobile";
